




















import { COLORS_NAMES, SIZES_NAMES } from 'utils/constants';
import SSpinner from 'components/atoms/spinner';

export default {
  name: 'SButton',

  components: {
    SSpinner
  },

  props: {
    color: {
      validator: (val) => val === null || Object.values(COLORS_NAMES).includes(val),
      default: 'white'
    },

    size: {
      validator: (val) => null || Object.values(SIZES_NAMES).includes(val),
      default: 'default'
    },

    disabled: {
      type: Boolean,
      default: false
    },

    icon: {
      type: Boolean,
      default: false
    },

    loading: {
      type: Boolean,
      default: false
    },

    round: {
      type: Boolean,
      default: false
    },

    outline: {
      type: Boolean,
      default: false
    }
  }
};
