



















































































import { INPUT_TYPES, INPUT_FEEDBACKS, SIZES_NAMES } from 'utils/constants';
import SIcon from 'components/atoms/icon.vue';

export default {
  name: 'SInput',

  components: {
    SIcon
  },

  props: {
    type: {
      validator: (val) => val === null || Object.values(INPUT_TYPES).includes(val),
      default: 'text'
    },

    name: {
      type: String,
      default: 's-input'
    },

    value: {
      type: String,
      default: ''
    },

    required: {
      type: Boolean,
      default: false
    },

    autofocus: {
      type: Boolean,
      default: false
    },

    optionalMarker: {
      type: String,
      default: '(optional)'
    },

    hasFeedback: {
      validator: (val) => val === null || Object.values(INPUT_FEEDBACKS).includes(val),
      default: ''
    },

    size: {
      validator: (val) => val === null || Object.values(SIZES_NAMES).includes(val),
      default: 'default'
    }
  }, // props

  data: () => ({
    INPUT_FEEDBACKS,
    INPUT_TYPES
  }),

  computed: {
    inputType(): string {
      if (this.type === INPUT_TYPES.SEARCH) {
        return INPUT_TYPES.TEXT;
      }

      return this.type;
    }
  },

  methods: {
    clear(evt: any): void {
      this.$emit('clear', evt);
      this.$refs.input.focus();
    }
  }
};
