











import { COLORS_NAMES, SIZES_NAMES } from 'utils/constants';

export default {
  name: 'SProgress',

  props: {
    color: {
      validator: (val) => val === null || Object.values(COLORS_NAMES).includes(val),
      default: 'primary'
    },

    size: {
      validator: (val) => null || Object.values(SIZES_NAMES).includes(val),
      default: 'default'
    },

    infinite: {
      type: Boolean,
      required: false
    },

    percentage: {
      type: [Number, String],
      default: 0
    },

    hidePercentage: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    percentageString(): string {
      if (this.infinite) {
        return '100%';
      }

      const value: string = String(this.percentage).replace(/[^0-9]/g, '');

      return `${value || 0}%`;
    }
  }
};
